import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"

import BrandStrong from "../components/layout/brandStrong"

const StyledAside = styled.aside`
  margin-bottom: 2rem;
  order: 2;
`

export default function GuideNav({ project, currentUID }) {
  const designGuides = project.guides.filter(
    guide => guide.Category === "design"
  )
  const codeGuides = project.guides.filter(guide => guide.Category === "code")
  const prodDev = project.product_development

  return (
    <StyledAside>
      <h3>Also in this project</h3>

      {designGuides.length > 0 && (
        <div>
          <BrandStrong>Design Guides</BrandStrong>
          <ul>
            {designGuides.map((guide, key) => (
              <li key={key}>
                {currentUID === guide.UID ? (
                  <strong>
                    <Link to={`/guides/${guide.UID}`}>{guide.Title}</Link>
                  </strong>
                ) : (
                  <Link to={`/guides/${guide.UID}`}>{guide.Title}</Link>
                )}
              </li>
            ))}
          </ul>
        </div>
      )}

      {codeGuides.length > 0 && (
        <div>
          <BrandStrong>Code Guides</BrandStrong>
          <ul>
            {codeGuides.map((guide, key) => (
              <li key={key}>
                {currentUID === guide.UID ? (
                  <strong>
                    <Link to={`/guides/${guide.UID}`}>{guide.Title}</Link>
                  </strong>
                ) : (
                  <Link to={`/guides/${guide.UID}`}>{guide.Title}</Link>
                )}
              </li>
            ))}
          </ul>
        </div>
      )}

      {prodDev && (
        <div>
          <BrandStrong>Product Development</BrandStrong>
          <ul>
            <li>
              {currentUID === prodDev.UID ? (
                <strong>
                  <Link to={`/product-development/${prodDev.UID}`}>
                    {prodDev.Title}
                  </Link>
                </strong>
              ) : (
                <Link to={`/product-development/${prodDev.UID}`}>
                  {prodDev.Title}
                </Link>
              )}
            </li>
          </ul>
        </div>
      )}
    </StyledAside>
  )
}
