import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import GuideNav from "../components/guideNav"

import TwoCols from "../components/layout/twoCols"
import Title from "../components/layout/title"
import BreadCrumbs from "../components/layout/breadCrumbs"

export default function GuideTemplate({ pageContext: guide }) {
  return (
    <Layout>
      <SEO title={`Guide - ${guide.Title}`} />

      <div>
        <BreadCrumbs>
          {[
            <Link to={`/projects`}>‚Üê All projects</Link>,
            <Link to={`/projects/${guide.project.UID}`}>
              {guide.project.Title}
            </Link>,
            guide.Title,
          ]}
        </BreadCrumbs>

        <Title>Guide: {guide.Title}</Title>

        <TwoCols>
          <div>
            <section>
              {guide.Description && (
                <div>
                  <h3>Description</h3>
                  <div
                    dangerouslySetInnerHTML={{ __html: guide.Description }}
                  />
                </div>
              )}
            </section>

            {(guide.Knowledge.length > 0 || guide.Skills.length > 0) && (
              <section>
                <TwoCols>
                  {guide.Knowledge.length > 0 && (
                    <div>
                      <h3>Knowledge</h3>
                      <ul>
                        {guide.Knowledge.map((item, key) => (
                          <li key={key}>{item.Knowledge}</li>
                        ))}
                      </ul>
                    </div>
                  )}

                  {guide.Skills.length > 0 && (
                    <div>
                      <h3>Skills</h3>
                      <ul>
                        {guide.Skills.map((item, key) => (
                          <li key={key}>{item.Skill}</li>
                        ))}
                      </ul>
                    </div>
                  )}
                </TwoCols>
              </section>
            )}

            <section>
              <h3>Topics</h3>
              <div dangerouslySetInnerHTML={{ __html: guide.topicsList }} />
            </section>

            <section>
              {guide.actionPoints.length > 0 && (
                <>
                  <h3>Action Points</h3>
                  <ul>
                    {guide.actionPoints.map((item, key) => (
                      <li key={key}>{item.actionPoint}</li>
                    ))}
                  </ul>
                </>
              )}
            </section>

            <section>
              <h3>
                Deliverable:{" "}
                <span
                  dangerouslySetInnerHTML={{ __html: guide.Deliver.Title }}
                ></span>
              </h3>

              <div
                dangerouslySetInnerHTML={{ __html: guide.Deliver.Description }}
              ></div>
            </section>

            {guide.Classes.length > 0 && (
              <section>
                <h3>Classes</h3>
                <ul>
                  {guide.Classes.map((item, key) => (
                    <li key={key}>
                      <a href={item.Link} target="_blank" rel="noreferrer">
                        {item.Title}
                      </a>
                    </li>
                  ))}
                </ul>
              </section>
            )}

            {guide.Resources.length > 0 && (
              <section>
                <h3>Resources</h3>
                <ul>
                  {guide.Resources.map((item, key) => (
                    <li key={key}>
                      <a href={item.Link} target="_blank" rel="noreferrer">
                        {item.Description}
                      </a>
                    </li>
                  ))}
                </ul>
              </section>
            )}
          </div>

          <GuideNav project={guide.project} currentUID={guide.UID} />
        </TwoCols>
      </div>
    </Layout>
  )
}
